/* src/main/frontend/src/components/NavBar.css */

/* 네비게이션 바의 기본 스타일 설정: 배경색, 내부 여백 */
.navbar {
    background-color: #333;
    padding: 1rem;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.navbar-list {
    list-style-type: none;
    margin: 0;
    padding: 0;
    width: 100%;
}

.navbar-item {
    width: 100%;
}

.navbar-link {
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
    display: block;
    width: 100%;
    text-align: center;
}

.navbar-link:hover {
    background-color: #555;
    border-radius: 0;
}
